table {
  border-spacing: 0;
  width: 100%;
  margin-bottom: 2.5rem;
}

td,
th {
  border-bottom: 0.1rem solid #e1e1e1;
  padding: 1.2rem 1.5rem;
  text-align: left;
}

td:first-child,
th:first-child {
  padding-left: 0;
}

td:last-child,
th:last-child {
  padding-right: 0;
}
thead {
  font-weight: 500;
}
